	module.exports = {"data":{"wordpressPage":{"title":"Screening for ectopic pregnancy","content":"<h2>Recommendation:</h2>\n<ul>\n<li>Ectopic pregnancy should be considered in women presenting for abortion who also have a concerning history or examination.</li>\n</ul>\n<p>&nbsp;</p>\n<h3>Strength of recommendation: Strong</h3>\n<h3></h3>\n<h3>Quality of evidence: Low</h3>\n<p>Epidemiology</p>\n<p>Data are scant from low resource settings regarding rates of ectopic pregnancy; in the United States, reported rates range from 1-2% of pregnancies (Stulberg, Cain, Dahlquist, &amp; Lauderdale, 2013; Tao, Patel, &amp; Hoover, 2016; Trabert, Holt, Yu, Van den Eeden, &amp; Scholes, 2011). Ectopic pregnancy accounts for 2.7% of pregnancy-related deaths in the United States (Creanga, Syverson, Seed, &amp; Callaghan, 2017). Ectopic pregnancy accounts for approximately 1% of pregnancy-related deaths in low resource settings where other causes of maternal death are more prevalent (Khan, Wojdyla, Say, Gulmezoglu, &amp; Van Look, 2006).</p>\n<p>Risk factors</p>\n<p>Factors with the highest associated risk of ectopic pregnancy in pregnant women are:</p>\n<table>\n<thead>\n<tr>\n<td>Risk factor</td>\n<td>Risk of ectopic in the current pregnancy​</td>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>​Previous ectopic pregnancy</td>\n<td>​10-15% (Yao &amp; Tulandi, 1997)</td>\n</tr>\n<tr>\n<td>History of tubal surgery, including sterilization</td>\n<td>​25-50% (Barnhart, 2009)</td>\n</tr>\n<tr>\n<td>Intrauterine device (IUD) in place​</td>\n<td>​25-50% (Barnhart, 2009)</td>\n</tr>\n</tbody>\n</table>\n<p>Other risk factors include a history of infertility and assisted reproductive technology, a history of pelvic infections, multiple partners, early age at first intercourse, and smoking. (Barnhart, 2009).</p>\n<p>Screening</p>\n<p>Half of all ectopic pregnancies occur in women with no risk factors and with a benign clinical presentation (Stovall, Kellerman, Ling, &amp; Buster, 1990). Providers should screen women for ectopic pregnancy risk factors during the history and physical examination including relevant history, such as previous ectopic pregnancy, tubal ligation, tubal surgery or an IUD in place. Screening should also include signs of ectopic pregnancy that can be found during physical examination, such as an adnexal mass or pain on examination or vaginal bleeding.</p>\n<p>Treatment for high-risk women</p>\n<p>Ultrasound and serial hCG testing are often used to help assess pregnancy location (Fields &amp; Hathaway, 2017). In some cases, the most expeditious way to confirm an intrauterine pregnancy is to perform vacuum aspiration; presence of products of conception in the uterine aspirate confirms an intrauterine pregnancy. A woman with suspicious signs and symptoms or a concerning physical exam should be diagnosed and treated as soon as possible or transferred immediately to a facility that can manage ectopic pregnancy. Early diagnosis and treatment of ectopic pregnancy can help preserve fertility and save women’s lives.</p>\n<p>Post-procedure screening</p>\n<p>For women undergoing vacuum aspiration, the aspirate should be strained and examined to confirm the presence of products of conception (see “Examining products of conception,” page XX). If products of conception are not seen, a diagnosis of ectopic pregnancy should be considered.</p>\n<h3>References</h3>\n<p>Barnhart, K. T. (2009). Clinical practice. Ectopic pregnancy. <em>New England Journal of Medicine</em>, <em>361</em>(4), 379-387.</p>\n<p>&nbsp;</p>\n<p>Creanga, A. A., Syverson, C., Seed, K., &amp; Callaghan, W. M. (2017). Pregnancy-related mortality in the United States, 2011-2013. <em>Obstetrics &amp; Gynecology, 130</em>(2), 366-373.</p>\n<p>&nbsp;</p>\n<p>Fields, L., &amp; Hathaway, A. (2017). Key concepts in pregnancy of unknown location: Identifying ectopic pregnancy and providing patient-centered care. <em>Journal of Midwifery and Womens Health, 62</em>(2), 172-179.</p>\n<p>&nbsp;</p>\n<p>Khan, K. S., Wojdyla, D., Say, L., Gulmezoglu, A. M., &amp; Van Look, P. F. (2006). WHO analysis of causes of maternal death: A systematic review. <em>The</em> <em>Lancet</em>, <em>367</em>(9516), 1066-1074.</p>\n<p>&nbsp;</p>\n<p>Stovall, T. G., Kellerman, A. L., Ling, F. W., &amp; Buster, J. E. (1990). Emergency department diagnosis of ectopic pregnancy. <em>Annals of Emergency Medicine</em>, <em>19</em>(10), 1098-1103.</p>\n<p>&nbsp;</p>\n<p>Stulberg, D., Cain, L. R., Dahlquist, I., &amp; Lauderdale, D. S. (2013). Ectopic pregnancy rates in the Medicaid population. <em>American Journal of Obstetrics &amp; Gynecology, 208</em>(4), 274.e1-7.</p>\n<p>Tao, G., Patel, C., &amp; Hoover, K. W. (2016). Updated estimates of ectopic pregnancy among commercially and Medicaid-insured women in the United States, 2002-2013. <em>Southern Medical Journal, 110</em>(1), 18-24.</p>\n<p>&nbsp;</p>\n<p>Trabert, B., Holt, V. L., Yu, O., Ven Den Eeden, S. K., &amp; Scholes, D. (2011). Population-based ectopic trends, 1993-2007. <em>American Journal of Preventative Medicine, 40</em>(5), 556-560.</p>\n<p>&nbsp;</p>\n<p>Yao, M., &amp; Tulandi, T. (1997). Current status of surgical and nonsurgical management of ectopic pregnancy. <em>Fertility and Sterility</em>, <em>67</em>(3), 421-433.</p>\n","fields":{"path":"before-13/screening-for-ectopic"},"acf":{"last_reviewed":"20171017","landing_page":false}},"parent":{"title":"Recommendations for abortion before 13 weeks gestation","fields":{"path":"before-13"},"wordpress_parent":159},"grandparent":null,"site":{"id":"Site","siteMetadata":{"title":"Ipas Clinical Updates"}}},"pathContext":{"id":"60e1b749-7036-553f-b62a-6c93dea34009","title":"Screening for ectopic pregnancy","content":"<h2>Recommendation:</h2>\n<ul>\n<li>Ectopic pregnancy should be considered in women presenting for abortion who also have a concerning history or examination.</li>\n</ul>\n<p>&nbsp;</p>\n<h3>Strength of recommendation: Strong</h3>\n<h3></h3>\n<h3>Quality of evidence: Low</h3>\n<p>Epidemiology</p>\n<p>Data are scant from low resource settings regarding rates of ectopic pregnancy; in the United States, reported rates range from 1-2% of pregnancies (Stulberg, Cain, Dahlquist, &amp; Lauderdale, 2013; Tao, Patel, &amp; Hoover, 2016; Trabert, Holt, Yu, Van den Eeden, &amp; Scholes, 2011). Ectopic pregnancy accounts for 2.7% of pregnancy-related deaths in the United States (Creanga, Syverson, Seed, &amp; Callaghan, 2017). Ectopic pregnancy accounts for approximately 1% of pregnancy-related deaths in low resource settings where other causes of maternal death are more prevalent (Khan, Wojdyla, Say, Gulmezoglu, &amp; Van Look, 2006).</p>\n<p>Risk factors</p>\n<p>Factors with the highest associated risk of ectopic pregnancy in pregnant women are:</p>\n<table>\n<thead>\n<tr>\n<td>Risk factor</td>\n<td>Risk of ectopic in the current pregnancy​</td>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>​Previous ectopic pregnancy</td>\n<td>​10-15% (Yao &amp; Tulandi, 1997)</td>\n</tr>\n<tr>\n<td>History of tubal surgery, including sterilization</td>\n<td>​25-50% (Barnhart, 2009)</td>\n</tr>\n<tr>\n<td>Intrauterine device (IUD) in place​</td>\n<td>​25-50% (Barnhart, 2009)</td>\n</tr>\n</tbody>\n</table>\n<p>Other risk factors include a history of infertility and assisted reproductive technology, a history of pelvic infections, multiple partners, early age at first intercourse, and smoking. (Barnhart, 2009).</p>\n<p>Screening</p>\n<p>Half of all ectopic pregnancies occur in women with no risk factors and with a benign clinical presentation (Stovall, Kellerman, Ling, &amp; Buster, 1990). Providers should screen women for ectopic pregnancy risk factors during the history and physical examination including relevant history, such as previous ectopic pregnancy, tubal ligation, tubal surgery or an IUD in place. Screening should also include signs of ectopic pregnancy that can be found during physical examination, such as an adnexal mass or pain on examination or vaginal bleeding.</p>\n<p>Treatment for high-risk women</p>\n<p>Ultrasound and serial hCG testing are often used to help assess pregnancy location (Fields &amp; Hathaway, 2017). In some cases, the most expeditious way to confirm an intrauterine pregnancy is to perform vacuum aspiration; presence of products of conception in the uterine aspirate confirms an intrauterine pregnancy. A woman with suspicious signs and symptoms or a concerning physical exam should be diagnosed and treated as soon as possible or transferred immediately to a facility that can manage ectopic pregnancy. Early diagnosis and treatment of ectopic pregnancy can help preserve fertility and save women’s lives.</p>\n<p>Post-procedure screening</p>\n<p>For women undergoing vacuum aspiration, the aspirate should be strained and examined to confirm the presence of products of conception (see “Examining products of conception,” page XX). If products of conception are not seen, a diagnosis of ectopic pregnancy should be considered.</p>\n<h3>References</h3>\n<p>Barnhart, K. T. (2009). Clinical practice. Ectopic pregnancy. <em>New England Journal of Medicine</em>, <em>361</em>(4), 379-387.</p>\n<p>&nbsp;</p>\n<p>Creanga, A. A., Syverson, C., Seed, K., &amp; Callaghan, W. M. (2017). Pregnancy-related mortality in the United States, 2011-2013. <em>Obstetrics &amp; Gynecology, 130</em>(2), 366-373.</p>\n<p>&nbsp;</p>\n<p>Fields, L., &amp; Hathaway, A. (2017). Key concepts in pregnancy of unknown location: Identifying ectopic pregnancy and providing patient-centered care. <em>Journal of Midwifery and Womens Health, 62</em>(2), 172-179.</p>\n<p>&nbsp;</p>\n<p>Khan, K. S., Wojdyla, D., Say, L., Gulmezoglu, A. M., &amp; Van Look, P. F. (2006). WHO analysis of causes of maternal death: A systematic review. <em>The</em> <em>Lancet</em>, <em>367</em>(9516), 1066-1074.</p>\n<p>&nbsp;</p>\n<p>Stovall, T. G., Kellerman, A. L., Ling, F. W., &amp; Buster, J. E. (1990). Emergency department diagnosis of ectopic pregnancy. <em>Annals of Emergency Medicine</em>, <em>19</em>(10), 1098-1103.</p>\n<p>&nbsp;</p>\n<p>Stulberg, D., Cain, L. R., Dahlquist, I., &amp; Lauderdale, D. S. (2013). Ectopic pregnancy rates in the Medicaid population. <em>American Journal of Obstetrics &amp; Gynecology, 208</em>(4), 274.e1-7.</p>\n<p>Tao, G., Patel, C., &amp; Hoover, K. W. (2016). Updated estimates of ectopic pregnancy among commercially and Medicaid-insured women in the United States, 2002-2013. <em>Southern Medical Journal, 110</em>(1), 18-24.</p>\n<p>&nbsp;</p>\n<p>Trabert, B., Holt, V. L., Yu, O., Ven Den Eeden, S. K., &amp; Scholes, D. (2011). Population-based ectopic trends, 1993-2007. <em>American Journal of Preventative Medicine, 40</em>(5), 556-560.</p>\n<p>&nbsp;</p>\n<p>Yao, M., &amp; Tulandi, T. (1997). Current status of surgical and nonsurgical management of ectopic pregnancy. <em>Fertility and Sterility</em>, <em>67</em>(3), 421-433.</p>\n","last_reviewed":"20171017","parent":163,"grandparent":null}}

